<script>
const VueHtml2pdf = () => import("vue-html2pdf");
import axios from "@//plugins//axios";

export default {
  components: {
    VueHtml2pdf,
  },

  data() {
    return {
      reportData: {},
    };
  },

  mounted() {
    this.getReport();
  },

  methods: {
    getReport() {
      const { id } = this.$route.params;
      if (!id) return;

      axios
        .get("get_declaracion_jurada_by_id/", {
          params: { id },
        })
        .then((resp) => {
          this.reportData = resp.data[0];

          // eslint-disable-next-line no-undef
          JsBarcode("#barcode", this.reportData.id, {
            displayValue: false,
            width: 3,
            height: 20,
          });

          //
          setTimeout(() => {
            this.$refs.html2Pdf.generatePdf();
          }, 100);
        })
        .catch((err) => {
          console.error(err);
        });
    },

    dowloading(e) {
      this.$emit("dowloading", e);
    },
  },
};
</script>
